/* Estilos generales */
body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
    background-image: url("../imagen/login1.jpg");
}

.login-container {
    background-color: #a09e9ed6;
    width: 250px;
    margin: 100px auto;
    padding: 100px;
    border-radius: 80px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    text-align: center;
}

.login-container h2 {
    margin-bottom: 20px;
    color: #333;
}

.login-container input[type="text"],
.login-container input[type="password"] {
    width: 200px;
    padding: 20px;
    margin-bottom: 10px;
    border: 1px solid #230000;
    border-radius: 80px;
}

.login-container button[type="submit"] {
    width: 240px;
    padding: 10px 20px;
    background-color: #000000;
    border-radius: 80px;
    color: #fff;
    
}

.login-container button[type="submit"]:hover {
    background-color: #6e6e6e;
}

.btn-container {
    margin-top: 20px;
    text-align: center;
}

.btn-container button {
    padding: 10px 20px;
    background-color: #000000;
    border: none;
    border-radius: 80px;
    color: #fff;
    cursor: pointer;
    
}

.btn-container button:hover {
    background-color: #6e6e6e;
}

/* Estilos para mensajes de error */
.error {
    color: #dc3545;
    font-size: 14px;
}

.logito{
    width: auto;
    height: 100px;
}